
// CSS stylings for leorobinovitch.com

body {
    margin: 0;
	font-family: Geneva,Tahoma,Verdana,sans-serif; 
    font-size: 18px;
    color: black;
}

// Typography

h1,
h2,
h3 {
    color: darkslategray;
}

h1 {
    margin: 25px 0px 0px;
    padding: 0;
}

h2 {
    margin: 15px 0px 0px;
}

header h2 {
    padding: 10px 0;
}

.post h2 {
    margin: 50px 0px 0px;
    padding: 0;
    border-color: black;
    border-style: groove none none;
    border-width: 1px;
}

a {
    text-decoration: none;
    color: blue;
}

a:not(.btn):hover,
a:not(.btn):focus,
a:not(.btn):active {
    font-style: italic;
}

.post-date {
    font-style: italic;
    font-size: 15px;
    margin-top: 10px;
    margin-bottom: 25px;
}

@media (prefers-color-scheme: dark) {
    h1,
    h2,
    h3,
    body {
        color: #e4e4e4;
    }

    header h2 {
        color: rgb(26, 26, 26);
    }

    body {
        background-color: #444;
    }

    a {
        color: #e39777;
    }

    img {
        filter: grayscale(30%);
    }
}

// Buttons

.btn {
    display: inline-block;
    padding: 8px 15px;
    color: darkslategray;
    background: #e3e3e3;
    text-decoration: none;
    border-radius: 2px;
    margin-right: 5px;
    margin-top: 5px;
}

.btn:hover,
.btn:focus,
.btn:active {
    color: #e3e3e3;
    background: black;
}

.btn-top {
    float: right;
    margin-top: 23px;
}

.btn-text:hover {
    font-style: italic;
}

#github, #linkedin {
    max-height: 27px;
    max-width: 30px;
    width: auto;
    height: auto;
    margin: 5px 5px 0px;
}

#github {
    margin-bottom: 1px;
}

#linkedin {
    max-height: 30px;
}

// Layout

header {
    background: rgb(122, 255, 166);
}

figure {
    margin: 20px 0px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    font-style: italic;
    font-size: 15px;
}

img {
    max-width: 95%;
}

footer {
    margin-top: 100px;
}

ul .post {
    margin: 10px 0;
}

.container {
    max-width: 700px;
    padding: 0 10px;
    margin: 0 auto;
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-wrap: wrap;
    margin-top: 20px;
}

.nav-buttons {
    display: flex;
    justify-content: right;
    align-items: flex-end;
    flex-wrap: wrap;
    margin-bottom: 5px;
}

.no-bull-list {
    margin: 10px 10px 30px 70px;
}

.fake-quote {
    text-indent: 40px;
}

.indent {
    margin-left: 40px;
}